* {
  margin: 0;
  padding: 0;
}
.projcon {
  display: flex;
  justify-content: center;
  align-items: center;
  height: max-content;
  /* background-color: aliceblue; */
}
.itemcontainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.card {
  height: 250px;
  width: 200px;
  background-color: rgb(0, 0, 0);
  padding: 10px;
  text-align: center;
  justify-content: space-around;
  scale: 1.3;
  margin: 50px;
  border-radius: 10px;
  box-shadow: rgba(203, 201, 210, 0.15) 0px 48px 100px 0px;
  position: relative;
    left: -100%;
    animation: slideRi2 1.5s forwards;
}

.card2 {
  height: 250px;
  width: 200px;
  background-color: rgb(0, 0, 0);
  padding: 10px;
  text-align: center;
  justify-content: space-around;
  scale: 1.3;
  margin: 50px;
  border-radius: 10px;
  box-shadow: rgba(203, 201, 210, 0.15) 0px 48px 100px 0px;
  position: relative;
    right: -100%;
    animation: slideRi 1.5s forwards;
}
.card-image {
  height: 100px;
  align-items: center;
  justify-content: center;
  width: 100px;
  border-radius: 10px;
}
.projh2 {
  text-align: center;
  font-size: 100px;
  position: relative;
  top: -100%;
  animation: slideTop 1.5s forwards;
}
@keyframes slideTop {
  0% {
    top: -30%;
  }
  100% {
    top: 0;
  }
}
@media (max-width: 950px) {
  body{
    overflow: hidden;
  }
  .projcon {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  .card {
    width: 40%;
  }

  .card2 {
    width: 40%;
  }
}